<template>
  <div class="AddNewBooks">
    <div class="back">
      <button @click="return_to_first_page" style="height: 200%;width: 200%;font-size: 100%;">back</button>
    </div>
    <div class="Header">
      <header>
        <h1>Material-ADD</h1>
      </header>
    </div>
  <div>

  </div>
    <div class="bookid">
      <label for="bookid">bookid:</label>
      <input v-model="bookid" id="bookid" type="text" style="height: 25px;font-size: 20px;width: 150px;text-align: center;" />
    </div>
    <div class="bookname">
      <label for="bookname">bookname:</label>
      <input v-model="bookname" id="bookname" type="text" style="height: 25px;font-size: 20px;width: 150px;text-align: center;" />
    </div>
    <div class="category">
      <label for="category">category:</label>
      <input v-model="category" id="category" type="text" style="height: 25px;font-size: 20px;width: 150px;text-align: center;" />
    </div>
    <div class="remain">
      <label for="remain">remain:</label>
      <input v-model="remain" id="remain" type="number" min="0" required />
    </div>
    <div class="introduction">
      <label for="introduction">introduction:</label>
      <textarea v-model="introduction" id="introduction" rows="15" style="vertical-align:middle;font-size: 20px; text-align: center;"></textarea>
    </div>
    <div class="Information">
      <div class="press">
        <label for="press">press:</label>
        <input v-model="press" id="press" type="text" style="height: 25px;font-size: 20px;width: 150px;text-align: center;" />
      </div>
      <div class="author">
        <label for="author">author:</label>
        <input v-model="author" id="author" type="text" style="height: 25px;font-size: 20px;width: 150px;text-align: center;" />
      </div>
      <div class="publishtime">
        <label for="publishtime">publishtime:</label>
        <input v-model="publishtime" id="publishtime" type="date" style="height: 25px;font-size: 20px;width: 200px;text-align: center;" />
      </div>
    </div>
    <div class="add">
      <button type="submit" @click="addNewBook" style="height: 50%;width: 50%;font-size: 100%;">Add Book</button>
    </div>

    <router-view />
  </div>
</template>

<script>
export default {
  data() {
    return {
      bookid: "",
      bookname: "",
      press: "",
      author: "",
      publishtime: "",
      category: "",
      remain: "",
      introduction: "",
    };
  },
  methods: {
    async addNewBook() {
      const url = `/staff/material/addNew/${this.bookid}/${this.bookname}/${this.press}/${this.author}/${this.publishtime}/${this.category}/${this.remain}/${this.introduction}`;
      const response = await fetch(url, { method: "POST" });
      if (response.ok) {
        alert("Book added successfully!");
        this.bookid = "";
        this.bookname = "";
        this.press ='';
        this.author = '';
        this.publishtime = '';
        this.category = '';
        this.remain = '';
        this.introduction = '';
      } else {
        alert('Failed to add book.');
      }
    },
    async return_to_first_page(){
      window.location.href="http://localhost:8080/staff.html"
    }
  }
}
</script>

<style>
 html,body {
     width: 100%;
     height: 100%;
     margin: 0;
     padding: 0;
}
body {
    min-width: 1024px;
    min-height: 600px;
    user-select: none; /* 禁止鼠标拖动页面时选中页面文字 */
}
.AddNewBooks{
  padding: auto;
	width:1600px;
	height:900;
	MARGIN-RIGHT: auto;
	MARGIN-LEFT: auto;
  position: absolute;
  /* border:0.2rem solid #4d4d4d */

}
  .Header {
    padding: 0% 10% 1%;
    /* border: 0.2rem solid #4d4d4d; */
    cursor: pointer;
    text-transform: capitalize;
    margin: 0% 5% 0% 18%;
    float: left;
  }
  .back{
    padding: auto;
    margin: 3% 10% 1% 5%;
    float: right;
    font-size: 200%;
  }
  .bookid{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 5% 0% 5%;
    float: left;
    font-size: 170%;
  }
  .bookname{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 0% 0% 0%;
    float: left;
    font-size: 170%;
  }
  .category{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 0% 0% 0%;
    float: left;
    font-size: 170%;
  }
  .remain{
    margin:5% 15% 0% 0%;
    font-size: 170%;
    float: right;
    /* border: 0.1rem solid #4d4d4d; */
  }
  .introduction{
    padding: auto;
    /* border:0.1rem solid #4d4d4d; */
    margin: 5% 5% 1% 8%;
    float: left;
    font-size: 200%;
  }

  .Information{
    width:10%;
    height: 40%;
    /* border: 0.1rem solid #4d4d4d; */
    margin: 5% 5% 5% 5%;
    padding:  auto;
    float:left;
    font-size: 180%;
  }
  .press{
    width:10%;
    height:30%;
    padding: auto;
    font-size: 100%;
  }
  .author{
    width:10%;
    height:30%;
    padding: auto;
    font-size: 100%;
  }
  .publishtime{
    width:10%;
    height:30%;
    padding: auto;
    font-size: 100%;
  }
  .add{
    width:15%;
    height:15%;
    padding: auto;
    font-size: 75%;
    float: right;
    margin: 13% 0 0 0;
  }



</style>



